input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.order__content-form {
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  position: relative;
}

.order__img-form-block {
  width: 500px;
  position: relative;
}

.order__img-form {
  border-radius: 30px;
  height: 700px;
  display: block;
  position: relative;
  box-shadow: 0 0 5px 5px #010101b3;
}

.order__form-order {
  z-index: 1;
  background-color: #28282899;
  border-radius: 30px;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  padding: 10px;
  display: flex;
  position: absolute;
  inset: 0;
}

.order__btn-back {
  z-index: 3;
  background-color: #dadada;
  position: absolute;
  top: 5px;
  left: 10px;
}

#order__contetn-input-name {
  margin-top: 100px;
}

.order__content-input {
  width: 70%;
}

.order__name-input {
  color: #fff;
  margin: 0 0 5px;
  padding: 0;
}

.order__input {
  background-color: #f5f5f5;
  border: none;
  border-radius: 5px;
  outline: none;
  width: 100%;
  padding: 5px;
  box-shadow: 0 0 5px 5px #000000b3;
}

.order__input:hover, .order__input:focus {
  box-shadow: 0 0 5px 5px #2a71fd80;
}

.order__span-products {
  color: #d63a3a;
}

.order__place-product {
  color: #2b2b2b;
  background-color: #c6c6c6;
  border-radius: 5px;
  width: 70%;
  max-height: 300px;
  padding: 5px;
  overflow-y: auto;
  box-shadow: 0 0 5px 5px #000000b3;
}

.order__place-product::-webkit-scrollbar {
  background-color: #a1a1a1;
  width: 10px;
}

.order__place-product::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 10px;
}

.order__submit {
  color: #fff;
  cursor: pointer;
  background-color: #2a71fd;
  border: none;
  border-radius: 10px;
  padding: 15px;
  font-weight: 700;
}

.order__submit:hover {
  color: #2a71fd;
  background-color: #fff;
  box-shadow: 0 0 5px 5px #2a71fd;
}

.text-info {
  text-align: center;
  color: #fff;
  background-color: gray;
  border-radius: 15px;
  padding: 7px;
  font-weight: 700;
  box-shadow: 0 0 5px 5px #000000b3;
}

.disable {
  color: #fff;
  background-color: #4f4f4f;
}

.disable:hover {
  color: #fff;
  cursor: default;
  background-color: #4f4f4f;
  box-shadow: 0 0 5px 5px #4f4f4f00;
}

@media (width <= 1000px) {
  .order__img-form-block {
    width: 380px;
    position: relative;
  }
}

@media (width <= 390px) {
  .order__img-form-block {
    width: 340px;
    position: relative;
  }
}

@media (width <= 345px) {
  .order__img-form-block {
    width: 310px;
    position: relative;
  }
}

@media (width <= 315px) {
  .order__img-form-block {
    width: 260px;
    position: relative;
  }
}

